import React, { useEffect } from 'react'
import { useParams } from "react-router-dom";
import { connect } from 'react-redux';


import ItemNotice from '../ItemNotice'
import { LayoutLottery } from "components";
import "./style.scss";



const DetailsNotice = ({
    noticeDetails,
    onGetDetailNotice
}) => {
    const params = useParams();
    useEffect(() => {
        onGetDetailNotice({
            id: Number(params?.id || 0)
        })
    }, [])

    return (
        <LayoutLottery>
            <div id="page-notice-details" className=" container-cst mt-5 pt-5 pb-5">
                <div className="notice-cont-detail">
                    <ItemNotice type='DETAILS' item={noticeDetails} />
                </div>

            </div>
        </LayoutLottery>
    )
}

export default connect(
    state => {
        const { common = {} } = state
        return {
            noticeDetails: common?.noticeDetails || {}
        }
    },
    ({ common: { onGetDetailNotice } }) => {
        return {
            onGetDetailNotice
        }
    }
)(DetailsNotice);
