import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";

import { BoxLottery } from "components";
import "./style.scss";
const ImageDefault = require("resources/images/img_notice.png");

const ItemNotice = ({ item, type }) => {
  const [state, setState] = useState({
    src: item?.mainimagelink || ImageDefault,
    errored: false,
  });

  //update(next img) state onMount
  useEffect(() => {
    setState({
      src: item?.mainimagelink || ImageDefault,
      errored: false,
    });
  }, [item]);

  //update (remove) state onUnMount
  useEffect(() => {
    return () => {
      setState({
        src: null,
        errored: false,
      });
    };
  }, []);

  const onError = () => {
    //1st error
    if (!state.errored) {
      setState({
        src: ImageDefault,
        errored: true,
      });
    } else if (state.errored && state.src) {
      //2nd error
      //when error on fallbacksrc - remove src
      setState({
        src: null,
        errored: true,
      });
    }
  };
  return (
    <div id="item-notice">
      <BoxLottery style={{ width: "100%", display: "flex" }} span={24}>
        <div className="item-content">
          <div className="top">
            <img
              src={state.src}
              onError={onError}
              style={type !== "DETAILS" ? { height: 146 } : {}}
            />
            <h3 className="f-16 mt-3 mb-4 line-clamp-1">{item?.title || ""}</h3>
            <div className="txt_con pb-4">
              <span className="line-clamp-1 only-desktop">
                {(type !== "DETAILS"
                  ? item?.shortcontent
                  : item?.fullcontent) || ""}
              </span>
              {type !== "DETAILS" && (
                <span className="only-mobile">{item?.fullcontent || ""}</span>
              )}
            </div>
          </div>
          <div className="bottom flex-center">
            <div className="flex-center right">
              <img src={require("resources/images/logo_tab.png")} />
              <span className="ml-2">관리자</span>
            </div>
            <div className="left">
              <span>{item?.dateConvert || ""}</span>
            </div>
          </div>
        </div>
      </BoxLottery>
    </div>
  );
};

ItemNotice.propTypes = {
  item: PropTypes.object,
  type: PropTypes.string,
};
export default ItemNotice;
